import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import HomeModel from "../../models/HomeModel";
import Swal from "sweetalert2";


function ModalACB(props) {


    const [modalShow, setModalShow] = React.useState(false);
    const [data, setData] = useState([]);
    useEffect(() => {
        HomeModel.getInfoHeader().then((res) => {
            setData(res.data);
        }).catch((err) => {
        })
    }, [])
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const copyToClipboard = () => {
        navigator.clipboard.writeText("40910727");
        Swal.fire({
            icon: "success",
            title: "Đã copy số tài khoản!",
            showConfirmButton: false,
            timer: 1000,
        });
    };
    const copyToClipboardTransactionCode = () => {
        navigator.clipboard.writeText(`okbill ${data.user_name}`);
        Swal.fire({
            icon: "success",
            title: "Đã copy nội dung chuyển khoản!",
            showConfirmButton: false,
            timer: 1000,
        });
    };
    return (
        <>
            <Modal
                {...props}
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                // style={{width:'500px', display:'flex' , justifyContent:'center'}}
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Nạp tiền tự động qua ACB
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className='row justify-content-center text-primary'>Cách 1: Chuyển khoản bằng mã QR<br />Mở app ngân hàng quét mã QRCode</div>
                    <div className='row pb-3'>
                        <div className='col-12 d-flex justify-content-center pt-3'>
                            <img src='/images/demoQR-bank-deposit/QR_ACB.png' width={300} height={300} />
                        </div>
                    </div>
                    <div className='row pt-3 border-top'>
                        <div className='row justify-content-center mb-5 text-primary'>Cách 2: Chuyển khoản thủ công theo thông tin</div>
                        <div className='col-12'>
                            <div className='pb-2'>Ngân hàng: Ngân hàng Thương Mại Cổ Phần Á Châu</div>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    Số tài khoản: 40910727
                                </div>
                                <div className='col-6' style={{height:'30px',width:'100px'}}>
                                    <button className='' onClick={copyToClipboard} style={{height:'100%', width:'100%' , borderRadius:'8px' , backgroundColor:'#16dee5', color:'white'}}>copy</button>
                                </div>
                            </div>
                            <div className='pb-2'>Tên tài khoản: NGUYEN TIEN TRIEU</div>
                            <div className='row pb-2'>
                                <div className='col-6'>Nội dung CK: <span className='text-primary'>okbill {data.user_name}</span></div>
                                <div className='col-6' style={{height:'30px',width:'100px'}}>
                                    <button className='' onClick={copyToClipboardTransactionCode} style={{height:'100%', width:'100%' , borderRadius:'8px' , backgroundColor:'#16dee5', color:'white'}}>copy</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='row pt-3'>
                        <div className='col-12'>
                            <div className='text-warning'>*Lưu ý: Nhập đúng nội dung chuyển khoản.<br/> Tiền sẽ được tự động cộng vào tài khoản trong tối đa 5 phút</div>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>


    );
}

export default ModalACB;